body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  background-color: black;
  font-family: Georgia;
  color: orange;
}


.container {
  padding: 0;
  margin: 0;
  float: left;
  width: 100%;
  height: 500px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow-y: hidden;
}

.wrap {
    max-width: 800px;
    margin: auto;
}

h1 {
    font-size: 3em;
    margin: 0 0 20px 0;
    text-align: center;
}


h2 {
  font-family: Georgia;
  font-size: 16em;
  color: orange;
  padding: 0;
  margin: 0;
}

#message,
#link {
    font-size: 1.5em;
    width: 100%;
    text-align: center;
}

.button {
    background-color: orange;
    color: black;
    margin-top: 20px;
    font-size: 1.5em;
    border-radius: 4px;
    width: 100%;
    padding: 10px 50px 10px 50px;
}

.button:hover {
    background-color: grey;
}

